@font-face {
  font-family: "Gotham Pro";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/GothamPro.woff2") format("woff2"), url("../fonts/GothamPro.woff") format("woff");
}

@font-face {
  font-family: "Gotham Pro";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/GothamPro-Light.woff2") format("woff2"), url("../fonts/GothamPro-Light.woff") format("woff");
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  min-width: 320px;
  max-width: 1920px;
  margin: 0 auto;
  font-family: "Gotham Pro", "Arial", "sana-serif";
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  padding: 0 40px;
  font-size: 15px;
  line-height: 17px;
  color: #ffffff;
  background-color: #00adef;
  border: none;
  border-radius: 24px;
  transition: all 0.2s ease;
}

.button:hover {
  cursor: pointer;
  background-color: #4687e0;
}

.button--white {
  color: #000000;
  background-color: #ffffff;
  border: 1px solid #e7e7e7;
}

.button--white:hover {
  color: #00adef;
  border-color: #00adef;
  background-color: #ffffff;
}

.authorization {
  position: relative;
  display: flex;
  margin: 0 auto;
  margin-top: 87px;
  width: 1170px;
  font-size: 0;
  background-color: #f6f6f6;
}

.authorization::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 1px;
  height: 360px;
  margin-top: -180px;
  background-color: #dddddd;
}

.authorization__form {
  width: 50%;
  padding: 62px 120px 70px;
}

.authorization__form-title,
.authorization__registration-title {
  margin: 0;
  margin-bottom: 42px;
  font-weight: 400;
  font-size: 31px;
  line-height: 34px;
}

.authorization__form label {
  display: inline-block;
  margin-bottom: 15px;
  font-weight: 300;
  font-size: 13px;
  line-height: 15px;
}

.authorization__form label[for="password"] {
  margin-right: 172px;
}

.authorization__forgot-password {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  display: inline-block;
  padding-bottom: 4px;
  font-weight: 300;
  font-size: 13px;
  line-height: 15px;
  color: #888888;
  text-align: right;
  border-bottom: 1px solid transparent;
  transition: all 0.2s ease;
}

.authorization__forgot-password:hover {
  border-color: #bbbbbb;
}

.authorization__form input {
  width: 330px;
  height: 48px;
  padding: 0 15px;
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  font-size: 19px;
  line-height: 25px;
  transition: all 0.3s ease;
}

.authorization__form input[name="name"] {
  margin-bottom: 18px;
}

.authorization__form input[name="password"] {
  margin-bottom: 42px;
}

.authorization__form input:hover, .authorization__form input:focus {
  border-color: #00adef;
  outline: none;
}

.authorization__button {
  width: 330px;
}

.authorization__registration {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
}

.authorization__registration-link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  width: 360px;
}

.account-header {
  background: transparent linear-gradient(180deg, #E7ECEC 0%, #E3EAEA 100%) 0% 0% no-repeat padding-box;
}

.account-header__container {
  margin: 0 auto;
  max-width: 1470px;
  padding: 92px 30px 30px;
  background-image: url("../img/midea.png");
  background-repeat: no-repeat;
  background-position: right 30px top 0;
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .account-header__container {
    background-image: url("../img/midea@2x.png");
    background-size: 690px 320px;
  }
}

.account-header__greeting {
  display: block;
  margin-bottom: 16px;
  font-weight: 300;
  font-size: 47px;
  line-height: 51px;
}

.account-header__description {
  margin: 0;
  margin-bottom: 126px;
  width: 570px;
  font-weight: 300;
  font-size: 15px;
  line-height: 25px;
}

.account-header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.account-header__nav-item {
  margin-right: 50px;
  margin-bottom: 9px;
  font-size: 19px;
  line-height: 21px;
  color: #888888;
  opacity: 0.8;
  transition: all 0.2s ease;
}

.account-header__nav-item--active {
  color: #00adef;
  opacity: 1;
}

.account-header__nav-item:hover {
  color: #00adef;
}

.account-header__nav-item a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.my-products__container {
  margin: 0 auto;
  max-width: 1470px;
  padding: 92px 30px 30px;
}
